<template>
	<view class="page">
		<ch-right-header :title="$t('次品日报')" path="/pages/report/badSearch"></ch-right-header>
		<view class="content bgwhite" style="padding-top:44px">
			<view v-if="!isGet">
				<mescroll-uni v-if="list.length > 0" ref="mescrollRef" @init="mescrollInit" :topbar="false" :fixed="false"
					@down="downCallback" @up="upCallback" :down="downOption" :up="upOption">
					<view class="order-wrap bgwhite">
						<view class="order-list ch-flex" v-for="(item, index) in list" :key="index"
							@click="itemClick(item)">
							<view class="ch-flex-1">
								<view class="ch-flex">
									<view class="label">{{ $t('派工单号') }}：</view>
									<view class="ch-flex-1 value">{{ item.dispatchNo }}</view>
								</view>
								<view class="ch-flex">
									<view class="label">{{ $t('产品编号') }}：</view>
									<view class="ch-flex-1 value">{{ item.itemNo }}</view>
								</view>
								<view class="ch-flex">
									<view class="label">{{ $t('产品名称') }}： </view>
									<view class="ch-flex-1 value">{{ item.itemName }}</view>
								</view>
								<view class="ch-flex">
									<view class="label">{{ $t('生产数') }}： </view>
									<view class="ch-flex-1 value">{{ item.produceCount }}</view>
								</view>
								<view class="ch-flex">
									<view class="label">{{ $t('次品数') }}：</view>
									<view class="ch-flex-1 value">{{ item.badCount }}</view>
								</view>
								<view class="ch-flex">
									<view class="label">{{ $t('次品率') }}：</view>
									<view class="ch-flex-1 value">{{ item.badRate * 100 }}%</view>
								</view>
							</view>
							<view class="rightWrap">
								<u-icon name="arrow-right" size="18" color="#bfbfbf"></u-icon>
							</view>
						</view>
					</view>
				</mescroll-uni>
				<view class="emptyView" v-else>
					<u-empty :text="$t('数据为空')" mode="list"></u-empty>
				</view>
			</view>
			<template v-else>
				<u-loading-icon text="loading"></u-loading-icon>
			</template>
		</view>
	</view>
</template>

<script>
import listScroll from "@/mixins/listScroll"
import Api from "@/api/bad/index.js"
export default {
	mixins: [listScroll],
	data() {
		return {
			apiUrl: Api.list,
			method: 'get',
			query: {},
		};
	},
	onShow() {
		uni.setNavigationBarTitle({ // 修改头部标题
			title: this.$t("bad.record")
		});
	},

	onLoad() {
		uni.$on('returnBadRecordData', data => {
			console.log('details发出的值===>', data);
			if (data) {
				this.query = {} //先清空上一次的搜索条件
				this.query = Object.assign({}, this.query, data)
				this.pager.pageNum = 1 //如果是搜索从第一页开始
				this.isGet = true
				this.getList();
			}
		});
	},
	onUnload() {
		uni.$off('returnBadRecordData')
	},
	methods: {
		//请求前组合参数
		beforeGetList() {
			this.list = []
		},

		//跳转次品详情
		itemClick(item) {
			uni.navigateTo({
				url: "/pages/report/badDetail" + '?dispatchNo=' + item.dispatchNo + '&dispatchPrior=' + item.dispatchPrior,
			})
		},
	},
}
</script>

<style lang="scss" scoped>
.order-list {
	align-items: center;
	justify-content: center;

	.serial {
		width: 60upx;
		height: 60upx;
		font-size: 24upx;
		color: #fff;
		background-color: $ch-theme;
		border-radius: 50%;
		margin: 0 24upx;
		text-align: center;
		line-height: 60upx;
	}

	.label {
		width: auto;
		color: #333;
	}
}
</style>